.user-searchbarChatUserSearch {
    /* -webkit-writing-mode: horizontal-tb !important; */
    text-rendering: auto;
    color: -internal-light-dark(black, white);
    letter-spacing: normal;
    word-spacing: normal;
    text-transform: none;
    text-indent: 0px;
    text-shadow: none;
    display: inline-block;
    text-align: start;
    appearance: auto;
    background-color: -internal-light-dark(rgb(255, 255, 255), rgb(59, 59, 59));
    -webkit-rtl-ordering: logical;
    cursor: text;
    margin: 0em;
    font: 400 13.3333px Arial;
    padding: 1px 2px;
    border-width: 2px;
    border-style: inset;
    border-color: -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
    border-image: initial;
    border-radius: 2px;
}

.user-info-itemChatUserSearch {
    list-style-type: none;
    color: white;
    margin-bottom: 80px;
    
}
.user-info-itemChatUserSearch:empty {
    display: none;
}

.result-listChatUserSearch {
    margin: 0px;
    padding: 10px;
    display: block; 
    
    /* list-style-type: disc; 
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 40px;  */
    border: white 2px solid;
    background-color: rgb(0, 0, 0);

}
.result-listChatUserSearch:empty {
    display: none;
}




.search-imageChatUserSearch {
    width: 100px;
    float: left;
    border: white 2px solid;
    /* border-radius: 22px; */
    
}

.search-imageChatUserSearch[src=""] {
    display: none;
}

/* This one will match when the user exists */
.user-display-nameChatUserSearch {
    color: white;
    display: block;
    padding-left: 125px;;
    font-size: 30px;
    font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;

}

/* This one will match when the image doesn't exist and we wnat to
say "User does not exist", without having that test be padded to the right */
.user-display-nameChatUserSearch_2 {
    color: white;
    display: block;
    font-size: 30px;
    font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;

}

.user-display-nameChatUserSearch:empty {
    display: none;
}

.result-containerChatUserSearch {
    position: absolute;
    z-index: 999;
    /* bottom: 18.5%; */
}

.result-containerChatUserSearch:empty {
    display: none;
}

